import React, { useState } from 'react';
import { Snackbar, TextField, Button, Grid, Typography } from '@material-ui/core';

const Video = () => {

  const [url, setURL] = useState("");

  const inputUrl = document.getElementById('url');

  function parseYoutubeId(videoUrl: string) {
    var reg1 = videoUrl.match(/youtube\..+?\/watch.*?v=(.*?)(?:&|\/|$)/);
    if (reg1 && reg1.length >= 2) {
      return reg1[1];
    }

    var reg2 = videoUrl.match(/youtu\.be\/(.*?)(?:\?|&|\/|$)/);
    if (reg2 && reg2.length >= 2) {
      return reg2[1];
    }

    var reg3 = videoUrl.match(/youtube\..+?\/embed\/(.*?)(?:\?|&|\/|$)/);
    if (reg3 && reg3.length >= 2) {
      return reg3[1];
    }

    return videoUrl;
  }

  const getVideo = (event: any) => {
    console.log('an event target', event.target);
    console.log('an event target value', event.target.value);
    setURL(event.target.value);
  }

  const redirect = () => {
    window.location.href = "/video?id=" + parseYoutubeId(url);
  }

  return (
    <Grid container justify="center">
      <Grid container item xs={5} justify="center">
        <Typography variant="h4">Speedrun Retimer</Typography>
        <TextField
          label="Video URL"
          value={url}
          id="url"
          onChange={(event) => getVideo(event)}
          fullWidth
        />
        <Button
          color="primary"
          variant="contained"
          onClick={() => redirect()}
        >
          Compute Time
        </Button>
      </Grid>
    </Grid>
  );
}

export default Video;